import React from 'react'
import Table from 'react-bootstrap/Table'

const Gloves = ({ gloves }) => {

    return (
        <Table striped bordered hover variant="dark">
            <thead>
                <tr>
                    <th>Name</th>
                    <th>Color</th>
                    <th>Price</th>
                    <th>Manufacturer</th>
                </tr>
            </thead>
            <tbody>
                {gloves.map(glove => (
                    <tr key={glove.id}>
                        <td>{glove.name}</td>
                        
                        <td>{glove.color.map(color =>
                            <span> {color} </span>)}</td>
                        <td>{glove.price}</td>
                        <td>{glove.manufacturer}</td>
                    </tr>)
                )

                }
            </tbody>
        </Table>
    )
}

export default Gloves