import React from 'react'
import Table from 'react-bootstrap/Table'

const Facemasks = ({ facemasks }) => {

    return (
        <Table striped bordered hover variant="dark">
            <thead>
                <tr>
                    <th>Name</th>
                    <th>Color</th>
                    <th>Price</th>
                    <th>Manufacturer</th>
                </tr>
            </thead>
            <tbody>
                {facemasks.map(mask => (
                    <tr key={mask.id}>
                        <td>{mask.name}</td>
                        
                        <td>{mask.color.map(color =>
                            <span> {color} </span>)}</td>
                        <td>{mask.price}</td>
                        <td>{mask.manufacturer}</td>
                    </tr>)
                )

                }
            </tbody>
        </Table>
    )
}

export default Facemasks