import React from 'react'
import Table from 'react-bootstrap/Table'

const Beanies = ({ beanies }) => {

    return (
        <Table striped bordered hover variant="dark">
            <thead>
                <tr>
                    <th>Name</th>
                    <th>Color</th>
                    <th>Price</th>
                    <th>Manufacturer</th>
                </tr>
            </thead>
            <tbody>
                {beanies.map(beanie => (
                    <tr key={beanie.id}>
                        <td>{beanie.name}</td>

                        <td>{beanie.color.map(color =>
                            <span> {color} </span>)}</td>
                        <td>{beanie.price}</td>
                        <td>{beanie.manufacturer}</td>
                    </tr>)
                )

                }
            </tbody>
        </Table>
    )
}

export default Beanies